/* Blockquote Styling */
blockquote {
  position: relative; /* Enable pseudo-elements positioning */
  /* border-left: 5px solid #007bff; */
  background-color: #f9f9f9; /* Light gray background */
  padding: 15px 20px; /* Space for the quote marks */
  margin: 10px 15px;
  font-style: italic;
  color: #555; /* Slightly muted text color */
  line-height: 1.6;
  font-family: 'Georgia', serif; /* Add a classic font for quotes */
}

/* Add opening quote using ::before */
blockquote::before {
  content: "“"; /* Unicode for left double quote */
  font-size: 2.5em; /* Bigger quote size */
  color: #007bff; /* Match the border color */
  position: absolute;
  top: -10px;
  left: 10px;
  font-weight: bold;
}

/* Add closing quote using ::after */
blockquote::after {
  content: "”"; /* Unicode for right double quote */
  font-size: 2.5em; /* Bigger quote size */
  color: #007bff; /* Match the border color */
  position: absolute;
  bottom: -10px;
  right: 15px;
  font-weight: bold;
}

/* Optional: Add hover effect for blockquote */
blockquote:hover {
  background-color: #e6f2ff; /* Slightly blue tint on hover */
}

/* Optional: Typography improvements */
blockquote p {
  margin: 0; /* Remove default margins inside blockquote */
  font-size: 1em;
}

/* Style links inside blockquote */
blockquote a {
  color: #007bff; /* Link color */
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

/* Hover effect for links in blockquote */
blockquote a:hover {
  color: #0056b3;
  text-decoration: underline;
}


