.syllabus-header {
  left: 0;
  right: 0;
  position: absolute;
  text-align: center;
  color: #007bff;
  font-size: 3em;
  margin: 30px 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3), 0px 0px 15px rgba(0, 123, 255, 0.7); /* Adds glow effect */
  -webkit-text-stroke: 1px #fff; /* White stroke for better contrast */
  font-family: 'Poppins', sans-serif; /* Clean and modern font */
  letter-spacing: 2px; /* Adds slight spacing between letters for elegance */
  animation: fadeIn 1s ease-in-out, glow 1.5s infinite alternate; /* Smooth fade-in and glowing animation */
}

/* Keyframes for fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Keyframes for glowing effect */
@keyframes glow {
  from {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3), 0px 0px 10px rgba(0, 123, 255, 0.4);
  }
  to {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3), 0px 0px 20px rgba(0, 123, 255, 1);
  }
}


