.tiles-wrapper {
  border-radius: 20px;
  box-shadow: rgba(0, 123, 255, 0.5) 0px 20px 20px;
  padding: 60px 20px 30px 20px;
  border: 1px solid #007bff;
  margin-bottom: 50px;
  /* background-color: #ffff; */
  position: relative;
  backdrop-filter: blur(15px);
}

.tiles-header {
    position: absolute;
    top: -30px; 
    backdrop-filter: blur(5px);
    background-color: #007bffde;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: rgba(0, 123, 255, 0.5) 0px 5px 5px;
    border: 1px solid #007bff;
    color: #fff;
    font-size: 1.8em;
    cursor: pointer;
    margin: 0;
}



@media screen and (max-width: 600px) {

  .rotate-right {
    transform: rotate(0deg)
  }
  
  .rotate-left {
    transform: rotate(0deg);
  }

  .syllabus-header {
    font-size: 1.2em;
    margin: 40px 0 50px 0; 
    -webkit-text-stroke: 0px #fff;
  }
  .tiles-wrapper {
    padding: 30px 10px 10px 10px;
    margin-bottom: 50px;
    margin-top: 50px;
    
  }
  .stack-grid-wrapper {
    margin: 25px 0;
  }
  .tiles-header {
    position: absolute;
    top: -25px;
    left: 0;
    backdrop-filter: blur(5px);
    background-color: #007bff;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: rgba(0, 123, 255, 0.5) 0px 5px 5px;
    border: 1px solid #007bff;
    color: #fff;
    font-size: 1em;
    cursor: pointer;
    margin: 0;
    right: 0;
    width: fit-content;
    margin: auto;
  }
}

/* syllabus style end */



@keyframes waveMovement {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spinReverse {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0.8;
  }
}

